/* styles.css */

/* Global Styles */
body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-color: #f7f7f7;
    color: #333;
}

/* Navigation Bar */
nav {
    background-color: #4ecdc4;
    padding: 1rem;
}

nav a {
    color: white;
    text-decoration: none;
    margin: 0 15px;
}

nav a:hover {
    text-decoration: underline;
}

/* Home Screen */
.home {
    text-align: center;
    padding: 50px 0;
}

/* Menu Styles */
.menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
}

.menu-item {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin: 10px;
    padding: 20px;
    width: 200px;
    text-align: center;
    transition: box-shadow 0.3s;
}

.menu-item:hover {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

/* Cart Styles */
.cart {
    padding: 20px;
}

.cart-item {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}

/* Contact Form */
.contact-form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: auto;
}

.contact-form input,
.contact-form textarea {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.contact-form button {
    background-color: #ff6b6b;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.contact-form button:hover {
    background-color: #ff4c4c;
}

/* Typography */
h1, h2, h3 {
    margin: 0;
}

h1 {
    font-size: 2rem;
}

h2 {
    font-size: 1.5rem;
}

h3 {
    font-size: 1.2rem;
}